<template>
	<view class="payment">
		<view class="payment-box"   @click="isshow=false">
			<view class="payment-c" @click.stop="isshow=true">
				<text class="totaltext">消费总额</text>
				<view class="total">
					<text>￥</text>
					<input type="digit" style="color: #00BFFF;" :value="number" @input="input" class="money" disabled="true" />
				</view>
				<text class="remark">可询问工作人员应缴费用总额</text>
			</view>
			<button class="submit">支付</button>
		</view>
		
		<!-- 弹出按键 -->
		<view class="key" v-show="isshow">
			<view class="num" @click="inputnum('1')">1</view>
			<view class="num" @click="inputnum('2')">2</view>
			<view class="num" @click="inputnum('3')">3</view>
			<view class="num" @click="inputnum('4')">4</view>
			<view class="num" @click="inputnum('5')">5</view>
			<view class="num" @click="inputnum('6')">6</view>
			<view class="num" @click="inputnum('7')">7</view>
			<view class="num" @click="inputnum('8')">8</view>
			<view class="num" @click="inputnum('9')">9</view>
			<view class="num" @click="inputnum('0')">0</view>
			<view class="num" @click="inputnum('.')">.</view>
			<view class="num delete" @click="remove">删除</view>
		</view>
	</view>

</template>

<script>
	export default {
		data() {
			return {
				number: '',
				isshow:false
			}
		},
		methods: {
			inputnum(val) {
				if (this.number == '0') {
					if (val != '.') {
						this.number = val
					} else {
						this.number += val
					}
					return;
				}else if (this.number.length == 0) {
					if (val == '.') {
						this.number = '0.'
					} else {
						this.number += val
					}
					return
				}else{
					 this.number+=val;
					 if(isNaN( this.number)){
					 	 this.number= this.number.substring(0, this.number.length-1)
					 }
				}
				var dot = this.number.indexOf(".");
				 if(dot != -1){
				                var dotCnt = this.number.substring(dot+1,this.number.length);
				                if(dotCnt.length > 2){
				                    // alert("小数位已超过2位！");
									this.number=this.number.substring(0,this.number.length-1)
				                }}
			},
			remove(){
				this.number = this.number.substring(0,this.number.length-1);
			},
			

		}
	}
</script>

<style lang="scss">
	@import '../../assets/css/payment/payment.scss';
</style>
